import { parse, isBefore } from 'date-fns';
import { id } from 'date-fns/locale';

export const compareDate = (schedule_date: string) => {
  const scheduleDate = parse(schedule_date, 'EEEE, dd MMMM yyyy', new Date(), { locale: id });
  const today = new Date();
  const isPastDate = isBefore(today, scheduleDate) || scheduleDate.getDate() === today.getDate();

  return isPastDate;
};
