import * as React from 'react';
import { clsx } from 'clsx';
import { ButtonBadgeProps } from './ButtonBadge.types';
import { CalendarIcon, ResultTestIcon } from '../../assets/svg';

const ButtonBadge: React.FC<ButtonBadgeProps> = ({ text, typeIcon, ...props }) => {
  return (
    <button
      {...props}
      className={clsx(
        'p-[10px] flex items-center space-x-[10px] rounded-[10px] border active:scale-95 duration-150 min-w-48 max-w-52',
        typeIcon === 'schedule' ? 'bg-[#F7FFF5] border-green-700' : 'bg-[#F7FAFF] border-blue-700'
      )}
    >
      {typeIcon === 'schedule' ? <CalendarIcon /> : <ResultTestIcon />}

      <p className="text-xs text-gray-600">{text}</p>
    </button>
  );
};

export default ButtonBadge;
