import * as React from 'react';
import { TPopUpScheduleDetail } from './PopUpScheduleDetail.types';
import api from '../../../../../utils/axios';
import { clsx } from 'clsx';
import toast, { Toaster } from 'react-hot-toast';
import { Dialog, DialogBody, DialogHeader, IconButton } from '@material-tailwind/react';
import { useReactToPrint } from 'react-to-print';
import { CalendarBlueIcon, XmarkIcon } from '../../assets/svg';
import { useScheduleContext } from '../../context/scheduleContext';
import { replacePlaceholders } from '../../utils/replace-placeholder';
import { compareDate } from '../../utils/compare-date';
import { IoCloseCircleOutline } from 'react-icons/io5';

export default function PopUpScheduleDetail({ isOpen, handleCloseScheduleDetail }: TPopUpScheduleDetail) {
  const { state } = useScheduleContext();

  const [dataScheduleDetail, setDataScheduleDetail] = React.useState([]); // Original data dari API
  const [isLoading, setIsLoading] = React.useState(false);
  const [activeDataScheduleDetail, setActiveDataScheduleDetail] = React.useState<any>([]); // Data setelah di olah dari API

  /**
   * Active button show detail schedule
   * dan sebagai filtering dari dataScheduleDetail
   */

  const [isActiveButton, setActiveButton] = React.useState(0);

  const [htmlContent, setHtmlContent] = React.useState<any>('');

  const [isValidTrial, setIsValidTrial] = React.useState(false);

  const containerRef = React.useRef<HTMLDivElement>(null);

  const getScheduleDetail = async () => {
    setIsLoading(true);
    try {
      const response = await api.post('/myapplication/get_detail_schedule', {
        applicant_id: state.applicant_id,
        vacancy_stage_id: state.vacancy_stage_id,
      });
      const result = response.data.data;
      setDataScheduleDetail(result);
    } catch (error) {
      console.log('🐙 ~ getScheduleDetail ~ error:', error);
    } finally {
      setIsLoading(false);
    }
  };

  React.useEffect(() => {
    if (isOpen) {
      getScheduleDetail();
    }
  }, [isOpen]);

  React.useEffect(() => {
    if (dataScheduleDetail.length > 0) {
      const dataScheduleDetailActive = dataScheduleDetail[isActiveButton];
      setActiveDataScheduleDetail(dataScheduleDetailActive);

      /**
       * Block fungsi untuk validasi current date <= date from api
       * hubungannya ke button start
       * untuk UI dan disabled juga
       */

      /** Awal fungsi validasi date */
      const validTrial = compareDate(dataScheduleDetailActive.schedule_date);
      setIsValidTrial(validTrial);
      /** Akhir fungsi validasi date */

      let { html_template } = dataScheduleDetailActive;

      html_template = replacePlaceholders(html_template, dataScheduleDetailActive);

      setHtmlContent(html_template);
    }
  }, [dataScheduleDetail, isActiveButton]);

  const handleClickCopy = (event) => {
    const targetId = event.target.id;
    let textToCopy = '';
    switch (targetId) {
      case 'btn_token':
        textToCopy = activeDataScheduleDetail.token;
        break;
      case 'btn_pass_seb':
        textToCopy = activeDataScheduleDetail.pass_seb;
        break;
      case 'btn_pass_exit_seb':
        textToCopy = activeDataScheduleDetail.pass_exit_seb;
        break;
      case 'btn_download_idcard':
        handlePrint();
      default:
        return;
    }

    navigator.clipboard
      .writeText(textToCopy)
      .then(() => {
        toast.success(`Copied`);
      })
      .catch((err) => {
        toast.error('Failed to copy text');
      });
  };

  React.useEffect(() => {
    const container = containerRef?.current;
    // const printListener = container?.querySelector('#idcard_panel');
    container?.addEventListener('click', handleClickCopy);

    return () => {
      container?.removeEventListener('click', handleClickCopy);
    };
  }, [htmlContent]);

  const resetState = () => {
    setActiveButton(0);
    setDataScheduleDetail([]);
  };

  const handleChangeActiveButton = (valueActive: number) => {
    setActiveButton(valueActive);
  };

  const handleClose = () => {
    handleCloseScheduleDetail();
    resetState();
  };

  // const handlePrint = () => {
  //   const container = containerRef?.current.querySelector('#idcard_panel');
  //   const
  // };
  const handlePrint = useReactToPrint({
    content: () => containerRef?.current.querySelector('#idcard_panel'),
  });

  return (
    <Dialog size="md" open={isOpen} className="flex flex-col max-h-[90vh] overflow-hidden">
      {isLoading ? (
        <div className="h-[100px] w-full bg-gray-200 rounded-md animate-pulse"/>
      ) : (
        <>
          <DialogHeader className="flex items-center justify-between space-x-2 sticky top-0 z-10 bg-white border-b">
            <div>
              <CalendarBlueIcon />
            </div>
            <h3 className="flex-1 font-bold text-sm">{activeDataScheduleDetail.title}</h3>
            <IconButton
              variant="text"
              size="sm"
              onClick={handleClose}
            >
                <IoCloseCircleOutline size={20}/>
            </IconButton>
          </DialogHeader>
          <DialogBody className="flex-1 p-4 font-normal text-gray-950 overflow-y-auto relative">
            <div className="flex flex-1 flex-wrap pb-2 gap-2">
              {dataScheduleDetail.map((item, index) => {
                return (
                  <button
                    key={index}
                    // disabled={isActiveButton !== 0}
                    onClick={() => handleChangeActiveButton(index)}
                    className={clsx(
                      'text-xs font-bold rounded-lg px-4 py-2.5 disabled:bg-gray-100 disabled:border-gray-100 disabled:text-gray-400',
                      isActiveButton === index
                        ? 'text-blue-700 bg-blue-50 border border-blue-700 '
                        : 'bg-white-100 border border-gray-300 text-gray-700 hover:bg-gray-50 duration-75'
                    )}
                  >
                    {item.tab_name}
                  </button>
                );
              })}
              {/* <button
                // disabled={isActiveButton !== 1}
                onClick={() => handleChangeActiveButton(1)}
                className={clsx(
                  'text-xs font-bold rounded-lg px-4 py-2.5 disabled:bg-gray-100 disabled:border-gray-100 disabled:text-gray-400',
                  isActiveButton === 1
                    ? 'text-blue-700 bg-blue-50 outline outline-blue-700 '
                    : 'bg-white-100 outline outline-gray-300 text-gray-700 hover:bg-gray-50 duration-75'
                )}
              >
                Online Test
              </button> */}
            </div>

            <hr />

            <div className="flex flex-col flex-1 pt-2 gap-2 overflow-y-auto">
              <div ref={containerRef} className="wysiwyg-card-detail my-2" dangerouslySetInnerHTML={{ __html: htmlContent }} />
              {activeDataScheduleDetail.stage_type !== 'interview' && (
                <footer>
                  <button
                    disabled={isValidTrial}
                    className={clsx(
                      'px-4 py-3  font-bold text-xs w-full rounded-lg',
                      isValidTrial ? 'bg-blue-700 text-white' : 'bg-red-50 text-red-700 cursor-not-allowed'
                    )}
                  >
                    {isActiveButton === 0 ? 'Start Trial Test' : 'Strat Online Test'}
                  </button>
                </footer>
              )}
            </div>
            <Toaster position="top-center" reverseOrder={false} toastOptions={{ className: 'z-50' }} />
          </DialogBody>
        </>
      )}
    </Dialog>
  );
}
